const translations = {
  'en': {
    'error.bad-tune': "Empty tune or unknown format",
    'play': 'Play',
    'pause': 'Pause',
    'stop': 'Stop'
  },

  'ru': {
    'error.bad-tune': '     ',
    'play': '',
    'pause': '',
    'stop': ''
  }
};

const lang = 'en';

/**
 * Translate by key
 *
 * @param key
 * @returns {string}
 */
const translate = key =>
  translations
  && translations[lang]
  && translations[lang][key]
  || key;

export default translate;
